.products-popup {
    background-color: #13405b;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    padding: 80px 0 105px;
    @include Sm {
        padding-top: 105px;
    }
    overflow-y: auto !important;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    &::-webkit-scrollbar {
        width: 0px !important;
    }
    &::-webkit-scrollbar-track {
        background-color: $color-sec !important;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: #12405a !important;
    }

    scrollbar-width: thin !important;
    scrollbar-color: rgba(#12405a, 1) rgba($color-sec, 1) !important;

    &.open {
        visibility: visible;
        opacity: 1;

        .offer-box {
            filter: none !important;
            transform: none !important;
            opacity: 1 !important;
            transition: 1s 0.3s !important;
        }
    }

    .searchBox {
        position: absolute;
        top: 0;
        left: 0;
        height: 40px;
        border-radius: 40px;
        padding: 0px;

        @include Sm {
            top: -80px;
        }

        .searchButton {
            float: left;
            color: transparent;
            float: right;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: transparent;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            border: 0;

            img {
                position: unset !important;
            }

            &:hover {
                img {
                    transform: none !important;
                }
            }
        }

        .searchInput {
            border: none;
            background: none;
            outline: none;
            float: right;
            padding: 0;
            color: white;
            font-size: 16px;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            line-height: 40px;
            border-bottom: 3px solid #d1a42e;
            width: 0px;
            margin-left: -3px;
            margin-top: -5px;

            @include Sm {
                margin-left: -4px;
                margin-top: -5px;
            }
        }

        @media screen and (max-width: 620px) {
            .searchBox:hover > .searchInput {
                width: 150px;
                padding: 0 6px;
            }
        }

        &:hover {
            > .searchInput {
                width: 240px;
                padding: 0 6px;
                // float: right;
            }
        }
    }

    .head {
        text-align: center;
        h1 {
            font-size: 40px;
            color: #fff;
            margin-bottom: 13px;
            font-family: bold;
            position: relative;

            @include Sm {
                display: flex;
                flex-direction: column-reverse;
            }

            img {
                position: absolute;
                top: 0;
                right: 0;
                cursor: pointer;
                transition: 0.5s;

                &:first-child {
                    &:hover {
                        transform: rotate(360deg);
                    }
                }

                &:last-child {
                    left: 0;
                    right: unset;
                }

                @include Sm {
                    top: -80px;
                }
            }
        }

        p {
            font-size: 16px;
            font-family: med;
            color: rgba(#fff, 0.6);
            margin-bottom: 70px;

            @include Md {
                width: 53%;
                margin-right: auto;
                margin-left: auto;
            }
        }
    }

    .top {
        border-radius: 70px 0 70px;
        margin-top: 70px;
        padding: 33px 56px;
        background-color: $color-sec;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include Sm {
            flex-direction: column;
            text-align: center;
        }

        .right {
            @include Md {
                width: 667px;
            }
        }

        h2 {
            font-size: 25px;
            font-family: bold;
            color: #fff;
            margin-bottom: 13px;
        }

        p {
            margin-bottom: 0;
            font-size: 16px;
            line-height: 25px;
            color: #fff;

            @include Sm {
                margin-bottom: 15px;
            }
        }

        a {
            border-radius: 100px;
            @extend .Flex;
            width: 144px;
            height: 60px;
            background-color: $color-prim;
            @extend .hover;
            font-size: 20px;
            font-family: med;
            color: #fff;
            padding-top: 5px;

            &:after,
            &:before {
                background-color: $color-prim;
            }
        }
    }

    .offers-section {
        background: none;

        .offer-box {
            transform: translate(100px, 100px);
            opacity: 0;
            transition: 0.4s;

            @include Sm {
                margin-bottom: 10px;
            }

            &.slide {
                transition: 0.4s !important;
                transform: translateY(50px) !important;
                opacity: 0 !important;
                visibility: hidden !important;
            }
        }

        .pro-slide {
            margin-bottom: 20px;
            background-color: #fff;
            text-align: center;
            border-radius: 0 45px 0 45px;
            overflow: hidden;
            margin-top: -96px;
            position: relative;
            z-index: -1;
            // transition: 0.5s;
            display: none;

            @include Sm {
                margin-top: -86px;
                border-radius: 0 56px 0 45px;
                margin-bottom: 10px;
            }
            &.slide {
                // margin-top: 0;
                z-index: unset;
                position: unset;
            }
            .pic {
                position: relative;
                cursor: pointer;
                overflow: hidden;

                > img {
                    width: 100%;
                    object-fit: cover;
                    transition: 0.7s;
                }

                &:hover {
                    > img {
                        transform: scale(1.05);
                    }
                }

                .label {
                    position: absolute;
                    top: 11px;
                    right: 15px;
                    width: 71px;
                    height: 60px;
                    background-color: $color-sec;
                    @extend .Flex;
                    border-radius: 0 40px 0 40px;
                }
            }

            h2 {
                font-family: bold;
                color: $color-prim;
                margin-top: 43px;
                font-size: 30px;
                margin-bottom: 23px;
                padding: 0 37px;
            }

            p {
                padding: 0 37px;
                font-size: 18px;
                font-family: med;
                color: #000000;
                margin-bottom: 28px;
            }

            .actions {
                padding: 0 37px;
                @extend .Flex;
                padding-bottom: 45px;
                position: relative;
                z-index: 9;

                a {
                    @extend .Flex;
                    width: 130px;
                    height: 50px;
                    @extend .hover;
                    background-color: $color-sec;
                    border-radius: 100px;
                    font-size: 18px;
                    font-family: med;
                    color: #fff;

                    &:last-child {
                        margin-right: 10px;
                        background-color: $color-prim;

                        &:after,
                        &:before {
                            background-color: $color-prim;
                        }
                    }
                }
            }
        }
    }

    .circles {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        z-index: -1;
    }

    .circles li {
        position: absolute;
        display: block;
        list-style: none;
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 0.2);
        animation: animate 25s linear infinite;
        bottom: -150px;
    }

    .circles li:nth-child(1) {
        left: 25%;
        width: 80px;
        height: 80px;
        animation-delay: 0s;
    }

    .circles li:nth-child(2) {
        left: 10%;
        width: 20px;
        height: 20px;
        animation-delay: 2s;
        animation-duration: 12s;
    }

    .circles li:nth-child(3) {
        left: 70%;
        width: 20px;
        height: 20px;
        animation-delay: 4s;
    }

    .circles li:nth-child(4) {
        left: 40%;
        width: 60px;
        height: 60px;
        animation-delay: 0s;
        animation-duration: 18s;
    }

    .circles li:nth-child(5) {
        left: 65%;
        width: 20px;
        height: 20px;
        animation-delay: 0s;
    }

    .circles li:nth-child(6) {
        left: 75%;
        width: 110px;
        height: 110px;
        animation-delay: 3s;
    }

    .circles li:nth-child(7) {
        left: 35%;
        width: 150px;
        height: 150px;
        animation-delay: 7s;
    }

    .circles li:nth-child(8) {
        left: 50%;
        width: 25px;
        height: 25px;
        animation-delay: 15s;
        animation-duration: 45s;
    }

    .circles li:nth-child(9) {
        left: 20%;
        width: 15px;
        height: 15px;
        animation-delay: 2s;
        animation-duration: 35s;
    }

    .circles li:nth-child(10) {
        left: 85%;
        width: 150px;
        height: 150px;
        animation-delay: 0s;
        animation-duration: 11s;
    }
}
