.about-popup {
    background-color: #13405b;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    padding: 80px 0 105px;
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    &::-webkit-scrollbar {
        width: 0px !important;
    }
    &::-webkit-scrollbar-track {
        background-color: $color-sec !important;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: #12405a !important;
    }

    scrollbar-width: thin !important;
    scrollbar-color: rgba(#12405a, 1) rgba($color-sec, 1) !important;

    &.open {
        visibility: visible;
        opacity: 1;

        .col-lg-5,
        .col-lg-6 {
            filter: none !important;
            transform: none !important;
            opacity: 1 !important;
            transition: 1s 0.3s;
        }
    }

    .head {
        text-align: center;
        h1 {
            font-size: 40px;
            color: #fff;
            margin-bottom: 13px;
            font-family: bold;
            position: relative;

            @include Sm {
                display: flex;
                flex-direction: column-reverse;
            }

            img {
                position: absolute;
                top: 0;
                right: 0;
                cursor: pointer;
                transition: 0.5s;

                @include Sm {
                    position: unset;
                    display: block;
                    margin: auto;
                    margin-bottom: 20px;
                }

                &:hover {
                    transform: rotate(360deg);
                }
            }
        }

        p {
            font-size: 16px;
            font-family: med;
            color: rgba(#fff, 0.6);
            margin-bottom: 70px;

            @include Sm {
                margin-bottom: 30px;
            }
        }
    }

    .row {
        display: flex;
        justify-content: space-between;

        > div {
            &:nth-child(1) {
                @include Sm {
                    order: 1;
                }
            }
            &:nth-child(2) {
                @include Sm {
                    order: 3;
                }
            }
            &:nth-child(3) {
                @include Sm {
                    order: 2;
                }
            }
            &:nth-child(4) {
                @include Sm {
                    order: 2;
                }
            }
            &:nth-child(5) {
                @include Sm {
                    order: 4;
                }
            }
            &:nth-child(6) {
                @include Sm {
                    order: 6;
                }
            }
        }

        .col-lg-5 {
            &:first-child {
                .pic {
                    svg {
                        width: 75px;
                        height: 60px;
                    }
                }
            }

            #Icon {
                fill: #fff;
            }
            .pic {
                background-color: $color-sec;
                @extend .Flex;
                flex-direction: column;
                height: 259px;
                border-radius: 0 70px 0 70px;
                border: 6px solid transparent;
                transition: 0.5s;

                h2 {
                    transform: translateZ(20px);
                    display: block;
                    width: 100%;
                    margin-top: 24px;
                    font-size: 35px;
                    color: #fff;
                    font-family: bold;
                    text-align: center;
                    transition: 0.5s;
                }

                svg {
                    path {
                        transition: 0.5s;
                    }
                }
            }

            &:hover {
                .pic {
                    background-color: transparent;
                    border: 6px solid $color-sec;

                    svg {
                        path {
                            fill: $color-sec !important;
                        }
                    }

                    h2 {
                        color: $color-sec;
                    }
                }
            }
        }

        .col-lg-6 {
            h2 {
                font-size: 35px;
                font-family: bold;
                color: $color-sec;
                @extend .after;
            }

            p {
                font-size: 18px;
                font-family: med;
                color: #fff;
                line-height: 35px;
            }
        }

        .col-lg-5,
        .col-lg-6 {
            transform: translateY(100px) scale(1.2);
            opacity: 0;
            margin-bottom: 70px;

            @include Sm {
                margin-bottom: 30px;
                text-align: center;
            }
        }
    }

    .numbers {
        padding-top: 56px;

        @include Sm {
            padding-top: 0;
        }

        .col-lg-4 {
            text-align: center;

            &:hover {
                span {
                    text-shadow: 3px 5px 1px rgba($color-white, 0.5);
                }
            }

            span {
                font-size: 100px;
                font-family: bold;
                color: #fff;
                display: block;
                line-height: 1;
                transition: 0.5s;
            }

            p {
                font-size: 30px;
                font-family: med;
                color: $color-sec;
            }
        }
    }

    .circles {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .circles li {
        position: absolute;
        display: block;
        list-style: none;
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 0.2);
        animation: animate 25s linear infinite;
        bottom: -150px;
    }

    .circles li:nth-child(1) {
        left: 25%;
        width: 80px;
        height: 80px;
        animation-delay: 0s;
    }

    .circles li:nth-child(2) {
        left: 10%;
        width: 20px;
        height: 20px;
        animation-delay: 2s;
        animation-duration: 12s;
    }

    .circles li:nth-child(3) {
        left: 70%;
        width: 20px;
        height: 20px;
        animation-delay: 4s;
    }

    .circles li:nth-child(4) {
        left: 40%;
        width: 60px;
        height: 60px;
        animation-delay: 0s;
        animation-duration: 18s;
    }

    .circles li:nth-child(5) {
        left: 65%;
        width: 20px;
        height: 20px;
        animation-delay: 0s;
    }

    .circles li:nth-child(6) {
        left: 75%;
        width: 110px;
        height: 110px;
        animation-delay: 3s;
    }

    .circles li:nth-child(7) {
        left: 35%;
        width: 150px;
        height: 150px;
        animation-delay: 7s;
    }

    .circles li:nth-child(8) {
        left: 50%;
        width: 25px;
        height: 25px;
        animation-delay: 15s;
        animation-duration: 45s;
    }

    .circles li:nth-child(9) {
        left: 20%;
        width: 15px;
        height: 15px;
        animation-delay: 2s;
        animation-duration: 35s;
    }

    .circles li:nth-child(10) {
        left: 85%;
        width: 150px;
        height: 150px;
        animation-delay: 0s;
        animation-duration: 11s;
    }
}
